
<template>
  <div class="ImageConvert">
    <div class="image-file-upload-section">
      <div class="image-file-drop-area" @click="triggerFileInput" @drop.prevent="handleDrop" @dragover.prevent>
        <h3>拖放图片到此处</h3>
        <p>或</p>
        <button class="image-select-btn"><i class="fas fa-file-image"></i> 选择图片</button>
        <input type="file" class="image-file-convert-in" ref="fileInput" @change="handleFileSelect" accept="image/*">
      </div>

      <div class="image-convert-controls" v-if="originalImage">
        <div class="image-convert-control-group">
          <label for="format">输出格式：</label>
          <select id="format" v-model="outputFormat">
            <option value="image/jpeg">JPEG</option>
            <option value="image/png">PNG</option>
            <option value="image/ico">ICO</option>
            <option value="image/svg">SVG</option>
            <option value="image/webp">WEBP</option>
            <option value="image/bmp">BMP</option>
            <option value="image/gif">GIF</option>
          </select>
        </div>

        <div class="image-convert-control-group" v-if="supportsQuality">
          <label for="quality">
            质量: <span class="range-value">{{ quality }}%</span>
          </label>
          <input type="range" id="quality" min="1" max="100" v-model="quality">
        </div>

        <div class="image-convert-control-group image-convert-control-size">
          <div>
            <label for="width">宽度 (px)</label>
            <input type="number" id="width" v-model.number="outputWidth" min="1" :max="maxWidth">
          </div>
          <div style="margin-left: 40px;" >
            <label for="height">高度 (px)</label>
            <input type="number" id="height" v-model.number="outputHeight" min="1" :max="maxHeight">
          </div>
        </div>
        <button class="image-select-btn" @click="convertImage" :disabled="converting">
          {{ converting ? '转换中...' : '转换图片' }}
        </button>
      </div>
    </div>

    <div class="image-convert-result-section" v-if="originalImage || convertedImage">
      <h2 class="image-convert-section-title">转换结果</h2>
      
      <div class="convert-preview-container">
        <div class="convert-preview-box" v-if="originalImage">
          <div class="convert-preview-title">原始图片</div>
          <div style="display: flex;min-width: 360px;">
            <img :src="originalImage" :style="{ width: displayWidth + 'px', height: displayHeight + 'px' }" class="convert-preview-image" alt="原始图片">
            <div class="convert-image-info">
              <div class="convert-info-row">
                <span>格式:</span>
                <span>{{ originalFormat.toUpperCase().replace('IMAGE/', '') }}</span>
              </div>
              <div class="convert-info-row">
                <span>尺寸:</span>
                <span>{{ originalWidth }} × {{ originalHeight }} px</span>
              </div>
              <div class="convert-info-row">
                <span>大小:</span>
                <span>{{ formatFileSize(originalSize) }}</span>
              </div>
            </div>
          </div>
        </div>

        <div class="convert-preview-box" v-if="convertedImage">
          <div class="convert-preview-title">转换后图片</div>
          <div style="display: flex;min-width: 360px;">
            <img :src="convertedImage" :style="{ width: displayWidth + 'px', height: displayHeight + 'px' }" class="convert-preview-image" alt="转换后图片">
            <div class="convert-image-info">
              <div class="convert-info-row">
                <span>格式:</span>
                <span>{{ outputFormat.toUpperCase().replace('IMAGE/', '') }}</span>
              </div>
              <div class="convert-info-row">
                <span>尺寸:</span>
                <span>{{ outputWidth }} × {{ outputHeight }} px</span>
              </div>
              <div class="convert-info-row">
                <span>大小:</span>
                <span>{{ formatFileSize(convertedSize) }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="conversion-info" v-if="convertedImage">
        <div class="size-comparison">
          <span>原始大小</span>
          <div class="size-bar">
            <div class="size-fill" :style="{ width: originalRate + '%' }"></div>
          </div>
          <span style="text-align: right;">{{ formatFileSize(originalSize) }}</span>
        </div>
        <div class="size-comparison">
          <span>转换后大小</span>
          <div class="size-bar">
            <div class="size-fill" :style="{ width: convertedRate + '%' }"></div>
          </div>
          <span style="text-align: right;">{{ formatFileSize(convertedSize) }}</span>
        </div>
      </div>

      <button class="image-select-btn image-select-btn-download" v-if="convertedImage" @click="downloadImage">
        <i class="fas fa-download"></i> 下载图片
      </button>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'ImageConvert',
    data() {
      return {
        originalImage: null,
        originalFormat: null,
        originalSize: 0,
        originalWidth: 0,
        originalHeight: 0,
        originalRate: 0,
        convertedImage: null,
        convertedSize: 0,
        convertedRate: 0,
        outputFormat: 'image/jpeg',
        quality: 80,
        outputWidth: 0,
        outputHeight: 0,
        maxWidth: 0,
        maxHeight: 0,
        converting: false,
        displayHeight: 0,
        displayWidth: 200
      }
    },
    computed: {
      supportsQuality() {
        // WEBP和JPEG支持质量参数
        return this.outputFormat === 'image/jpeg' || this.outputFormat === 'image/webp';
      }
    },
    watch: {
      outputWidth(newWidth) {
        if (this.originalWidth && this.originalHeight) {
          this.outputHeight = Math.round((newWidth / this.originalWidth) * this.originalHeight);
        }
      },
      outputHeight(newHeight) {
        if (this.originalWidth && this.originalHeight) {
          this.outputWidth = Math.round((newHeight / this.originalHeight) * this.originalWidth);
        }
      }
    },
    methods: {
      triggerFileInput() {
        this.$refs.fileInput.click();
      },
      handleDrop(e) {
        const files = e.dataTransfer.files;
        if (files.length) {
          this.processImage(files[0]);
        }
      },
      handleFileSelect(e) {
        const files = e.target.files;
        if (files.length) {
          this.processImage(files[0]);
        }
      },
      processImage(file) {
        if (!file.type.match('image.*')) {
          alert('请选择图片文件！');
          return;
        }

        const reader = new FileReader();
        reader.onload = (e) => {
          this.originalImage = e.target.result;
          this.originalSize = file.size;
          this.originalFormat = file.type;
          
          // 获取图片原始尺寸
          const img = new Image();
          img.onload = () => {
            this.originalWidth = img.width;
            this.originalHeight = img.height;
            this.outputWidth = img.width;
            this.outputHeight = img.height;
            this.maxWidth = img.width * 2;
            this.maxHeight = img.height * 2;
            let dHeight = (this.displayWidth / this.originalWidth) * this.originalHeight;
            if(dHeight > 120) {
              this.displayHeight = 120;
              this.displayWidth = (this.displayHeight / this.originalHeight) * this.originalWidth;
            } else {
              this.displayHeight = dHeight;
            }
          };
          img.src = e.target.result;
        };
        reader.readAsDataURL(file);
      },
      convertImage() {
        if (!this.originalImage) return;
        
        this.converting = true;
        
        // 使用setTimeout让UI有机会更新转换中的状态
        setTimeout(() => {
          const img = new Image();
          img.onload = () => {
            const canvas = document.createElement('canvas');
            const ctx = canvas.getContext('2d');
            
            // 设置canvas尺寸
            canvas.width = this.outputWidth;
            canvas.height = this.outputHeight;
            
            // 绘制图片到canvas
            ctx.drawImage(img, 0, 0, this.outputWidth, this.outputHeight);
            
            // 转换为目标格式
            let dataURL;
            if (this.supportsQuality) {
              dataURL = canvas.toDataURL(this.outputFormat, this.quality / 100);
            } else {
              dataURL = canvas.toDataURL(this.outputFormat);
            }
            
            this.convertedImage = dataURL;
            
            // 计算转换后文件大小
            this.convertedSize = this.getDataURLSize(dataURL);
            if(this.convertedSize > this.originalSize) {
              this.originalRate = (this.originalSize / this.convertedSize) * 100;
              this.convertedRate = 100;
            } else {
              this.convertedRate = (this.convertedSize / this.originalSize) * 100;
              this.originalRate = 100;
            }
            
            this.converting = false;
          };
          img.src = this.originalImage;
        }, 100);
      },
      getDataURLSize(dataURL) {
        // 估算Base64编码的图片大小
        const base64 = dataURL.split(',')[1];
        const approximateSize = Math.floor((base64.length * 3) / 4);
        return approximateSize;
      },
      downloadImage() {
        if (!this.convertedImage) return;
        
        const link = document.createElement('a');
        link.href = this.convertedImage;
        
        // 根据格式生成文件名
        const extension = this.outputFormat.split('/')[1];
        link.download = `converted-image.${extension}`;
        
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      },
      formatFileSize(bytes) {
        if (bytes < 1024) {
          return bytes + ' B';
        } else if (bytes < 1048576) {
          return (bytes / 1024).toFixed(2) + ' KB';
        } else {
          return (bytes / 1048576).toFixed(2) + ' MB';
        }
      }
    }
  }
</script>
<style>
  .ImageConvert {
    display: flex;
    flex-wrap: wrap;
    background-color: #ffffff;
    background-size: 100% 100%;
    justify-content: center;
  }
  .image-file-upload-section, .image-convert-result-section {
    background: white;
    padding: 25px;
    flex: 1;
    min-width: 300px;

    span {
      margin-left: 4px;
    }
  }
  .image-convert-section-title {
    color: #3498db;
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 0px;
    margin-bottom: 10px;
  }
  .image-convert-section-title i {
    font-size: 1.5rem;
  }
  .image-file-drop-area {
    border: 2px dashed #3498db;
    border-radius: 8px;
    padding: 40px 20px;
    text-align: center;
    cursor: pointer;
    transition: all 0.3s;
    margin-bottom: 20px;
  }
  .image-file-drop-area:hover {
    background-color: #f8f9fa;
    border-color: #2980b9;
  }
  .image-file-drop-area i {
    font-size: 3rem;
    color: #3498db;
    margin-bottom: 15px;
  }
  .image-file-drop-area p {
    margin: 10px 0;
    color: #7f8c8d;
  }
  .image-file-convert-in {
    display: none;
  }
  .image-select-btn {
    background: #3498db;
    color: white;
    border: none;
    padding: 12px 24px;
    border-radius: 6px;
    cursor: pointer;
    font-size: 1rem;
    transition: background 0.3s;
    display: inline-flex;
    align-items: center;
    gap: 8px;
  }
  .image-select-btn:hover {
    background: #2980b9;
  }
  .image-select-btn:disabled {
    background: #bdc3c7;
    cursor: not-allowed;
  }
  .image-select-btn-download {
    margin-top: 10px;
    background: #2ecc71;
  }
  .image-select-btn-download:hover {
    background: #27ae60;
  }
  .image-convert-controls {
    margin: 20px 0;
    
    .image-convert-control-group {
      margin-bottom: 10px;
    }
    .image-convert-control-size {
      display: flex;
    }

    label {
      display: block;
      font-weight: 500;
      color: #2c3e50;
    }
    
    select, input[type="range"] {
      margin: 2px 0px;
      width: 100%;
      border: 1px solid #ddd;
      border-radius: 6px;
      font-size: 1rem;
    }
  }
  .range-value {
    display: inline-block;
    width: 40px;
    text-align: right;
  }
  .convert-preview-container {
    display: flex;
    flex-wrap: wrap;
  }
  .convert-preview-box {
    flex: 1;
    min-width: 360px;
  }
  .convert-preview-title {
    font-size: 0.9rem;
    color: #7f8c8d;
    margin-bottom: 8px;
  }
  .convert-preview-image {
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    border: 1px solid #eee;
  }
  .convert-image-info {
    display: block;
    background: #f8f9fa;
    padding: 15px;
    border-radius: 8px;
    font-size: 0.9rem;
    margin-left: 8px;
    width: 300px;
    min-width: 150px;
  }
  .convert-info-row {
    display: flex;
    justify-content: space-between;
    margin: auto;
  }
  .conversion-info {
    background: #e8f4fc;
    padding: 10px 15px;
    border-radius: 8px;
    margin-top: 10px;
    font-size: 0.9rem;
  }
  .size-comparison {
    display: flex;
    align-items: center;
    margin-top: 10px;
    gap: 10px;
    span {
      width: 80px;
    }
  }
  .size-bar {
    flex-grow: 1;
    height: 8px;
    background: #ecf0f1;
    border-radius: 4px;
    overflow: hidden;
  }
  .size-fill {
    height: 100%;
    background: #3498db;
    border-radius: 4px;
  }
  @media (max-width: 768px) {
    .ImageConvert {
      flex-direction: column;
    }
  }
</style>